html{
    height: 100vh;
    background: linear-gradient(to bottom, #ff9c2f, #d42525);
    display: flex;
    justify-content: center;
    align-items: center;
    border: 15px solid;
    border-color: ghostwhite;
}
body {
    height: 100vh;
    background: linear-gradient(to bottom, #ff9c2f, #d42525);
    display: flex;
    justify-content: center;
    align-items: center;
}

.zone-container {
    margin-top: 10%;
    margin-bottom: 10%;
}

.header {
    padding: 2%;
    text-align: center;
}

h1 {
    font-family: Bitter;
    font-size: 60px;
    font-style: normal;
    font-variant: normal;
    color: whitesmoke;
    font-weight: bold;
    line-height: 93.1px;
}